body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  background: black;
  width: 100%;
  height: 100%; }

#link-to-github {
  display: block;
  position: absolute;
  bottom: 10px;
  right: 10px;
  background: #170B89;
  border: solid white 3px;
  color: white;
  text-decoration: none;
  padding: 0.25em;
  font-weight: bold; }

#svg-container {
  position: fixed;
  width: 100vmin;
  height: 100vmin;
  top: 0;
  left: calc(50% - 50vmin);
  overflow: hidden;
  background: #30014F;
  background: radial-gradient(ellipse at center, #30014F, #170B89); }
  #svg-container svg {
    transition: viewBox 3s linear, -webkit-transform 3s linear;
    transition: transform 3s linear, viewBox 3s linear;
    transition: transform 3s linear, viewBox 3s linear, -webkit-transform 3s linear; }
  #svg-container circle {
    fill: transparent;
    stroke: white; }

#render-options {
  position: absolute;
  top: 10px;
  left: 10px;
  color: white;
  z-index: 1; }
  #render-options div {
    background: white;
    border: solid #170B89 3px;
    color: black;
    font-weight: bold;
    margin-bottom: 1em;
    padding: 0.25em;
    cursor: pointer; }
  #render-options .reset {
    color: red; }

